.form {
  position: relative;
  padding-top: 1rem;
  min-height: 500px;
  background-image: url("../img/form/m-bg.jpg");
  background-size: 100% 100%;
  background-position: 0 0;
  background-repeat: no-repeat;

  @media (max-width: 560px) {
    max-width: 300px;
    margin: 0 auto;
  }
  @media (min-width: 560px) {
    background-image: url("../img/form/form.png");
  }

  &__inner {
    margin: 2rem 1.5rem;
    background-color: #fff;
    border: none;
    overflow: hidden;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 100%;
    text-align: center;

    @media (min-width: 560px) {
      margin-left: 29%;
      margin-right: 37%;
    }

    @include for-size(tablet-portrait-up) {
      max-width: 100%;
      max-height: 450px;
      margin-left: 29%;
      margin-right: 37%;
    }
    @include for-size(desktop-up) {
      max-width: 100%;
      margin-left: 29%;
      margin-right: 37%;
    }
  }

  &__list {
    position: relative;
    z-index: 3;
    padding: 0;
    margin: 0;
    list-style-type: none;
    overflow-y: auto;
    height: 100%;
    max-width: 100%;
  }

  &__item {
    margin-bottom: 0.7rem;
  }

  &__field {
    padding: 5px;
    width: 100%;
    border-radius: 0;
    border: none;
    box-shadow: 0 3px 3px -3px #333;

    &:focus,
    &:hover {
      box-shadow: 0 3px 3px -3px rgb(133, 67, 153);
    }

    &:focus::-webkit-input-placeholder,
    &:hover::-webkit-input-placeholder {
      color: rgb(133, 67, 153);
    }
    &:focus::-moz-placeholder,
    &:hover::-moz-placeholder {
      color: rgb(133, 67, 153);
    }
    &:focus:-moz-placeholder,
    &:hover:-moz-placeholder {
      color: rgb(133, 67, 153);
    }
    &:focus:-ms-input-placeholder,
    &:hover:-ms-input-placeholder {
      color: rgb(133, 67, 153);
    }
  }
  &__comment {
    padding: 5px;
    width: 100%;
    resize: none;
    border-radius: 0;
    border: none;
    box-shadow: 0 3px 3px -3px #333;
  }

  & .button {
    margin-top: 1rem;
  }

  & h3 {
    font-size: 1rem;
  }
}
