.button {
  display: inline-block;
  vertical-align: middle;
  padding: $btn-padding;
  @include ff("font-size", $btn-fz);
  font-family: $mainFont-bold;
  font-weight: $mainFontWeight-bold;

  color: #ffffff;
  background: $btn-bg-color;
  border-radius: 20px;

  box-shadow: 0 0 20px 2px rgb(155, 68, 153);
  text-transform: uppercase;
  user-select: none;
  cursor: pointer;
  border: none;

  outline: none;

  transition: 1s;

  &:focus,
  &:hover {
    outline: 0;
    // background-color: darker($btn-bg-color, 2.5%);
    opacity: 0.75;
  }
}
