.slider {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    z-index: 2;
    bottom: 0;
    right: 0;
    height: 120px;
    width: 120px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("../img/slider/cow.png");

    @include for-size(tablet-portrait-up) {
      height: 200px;
      width: 200px;
    }
  }

  & .swiper-container {
    height: 100%;
    width: 100%;
    display: none;
    @include for-size(tablet-portrait-up) {
      display: block;
    }
  }
  & .swiper-slide {
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center bottom;

    &::after {
      content: "";
      position: absolute;
      // z-index: 1;
      bottom: 0;
      right: 0;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background-size: 100%;
      background-repeat: repeat-x;
      background-position: center;
      background-image: url("../img/slider/slider-bg.png");
    }
  }
}
