.section {
  position: relative;
  display: flex;
  flex-direction: column;

  &__inner {
    @include grid-row();
    @include ff("padding-top", $pt-size);
    @include ff("padding-bottom", $pt-size);
  }
  &__info {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 1.25rem;


    @include grid-col(
      (
        xs: 12,
        sm: 5,
        md: 6
      )
    );

    @include for-size(tablet-portrait-up) {
      justify-content: flex-start;
      margin-bottom: 0;
    }
  }
  &__pic {
    position: relative;
    width: 100%;
    height: 350px;
    background-repeat: no-repeat;
    background-position: top;
    background-size: contain;
    @include grid-col(
      (
        xs: 12,
        sm: 7,
        md: 6
      )
    );

    @include for-size(tablet-portrait-up) {
      position: absolute;
      right: 0;
      top: 0;
      min-height: 20rem;
      transform: translate(150%, 0);
      background-position: center;
    }

    &::before {
      content: '';
      position: absolute;
      z-index: 1;
      bottom: 0;
      right: 40%;
      width: 12rem;
      height: 15rem;
      background-repeat: no-repeat;
      background-position: top;
      background-image: url('../img/trading/cow.png');
      background-size: contain;
      transform: translate(40%, 0%);

      @include for-size(tablet-portrait-up) {
        bottom: -45px;
        background-position: center;
      }
    }
  }
  &__caption {
    margin-bottom: 2.5rem;
    font-family: $mainFont-semibold;
    @include ff("font-size", $subtitle-fz);

    opacity: 0.5;
    color: #854399;
    text-transform: uppercase;
    letter-spacing: 6px;
  }
  &__text {
    margin-bottom: 3rem;
  }
  & .title {
    max-width: 28rem;
  }
  &__form {
    margin-top: 2rem;
    @include grid-col(
      (
        xs: 12,
        sm: 7,
        md: 6,
      )
    );

    @include for-size(tablet-portrait-up) {
      margin-top: 0
    }
  }
  & .button {
    align-self: center;
    @include for-size(tablet-portrait-up) {
      align-self: flex-start;
    }
  }
}
