.promo {
  position: relative;
  padding-top: 2rem;
  margin-bottom: var(--mb);
  color: #ffffff;

  background: linear-gradient(
    0deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(60, 36, 64, 1) 52%,
    rgba(115, 52, 107, 1) 100%
  );

  background-image: url("../img/promo/background.jpg");
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;

  background-color: rgba(60, 36, 64, 1);

  @include for-size(tablet-portrait-up) {
    padding-top: 5rem;
  }

  &__inner {
    @include grid-row();
    min-height: 50vh;
    @include for-size(tablet-portrait-up) {
      min-height: 75vh;
    }
  }

  &__logo {
    @include grid-col(
      (
        xs: 12
      )
    );

    @include for-size(tablet-portrait-up) {
      margin-bottom: 5rem;
    }
  }
  &__text {
    margin-bottom: 3rem;
    @include grid-col(
      (
        xs: 12,
        sm: 6,
        md: 7
      )
    );
    @include for-size(tablet-portrait-up) {
      margin-bottom: 5rem;
    }
  }

  & .button {
    margin-top: 3rem;
  }

  &__slider {
    @include grid-col(
      (
        xs: 12,
        sm: 6,
        md: 5
      )
    );
  }
}
