.trading {
  position: relative;

  &::before,
  &::after {
    content: '';
    position: absolute;
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 100%;
    width: 100px;
    height: 100px;
    z-index: 0;
  }
  &::before {
    top: 20%;
    left: 0;
    height: 200px;
    width: 200px;
    background-image: url('../img/trading/bg-box-left.png');
  }
  &::after {
    top: 30%;
    right: 15px;
    background-image: url('../img/trading/bg-box-right.png');
  }
  & .container {

    background-image: url('../img/trading/bg-line.png');
    background-repeat: no-repeat;
    background-position: 95% bottom;
    background-size: 40%;
  }
}
