.link {
  display: inline-block;
  vertical-align: middle;
  background-color: transparent;
  line-height: normal;
  color: #76c0f3;
  border-radius: 0;
  text-decoration: none;
  transition: 0.4s;

  &:focus,
  &:hover {
    text-decoration: none;
    color: darker(#76c0f3, 2.5%);
  }


}
