// контрольные точки
// пример использования
// .my-box {
//   padding: 10px;

//   @include for-size(desktop-up) {
//     padding: 20px;
//   }
// }
@mixin for-size($size) {
  @if $size == phone-only {
    @media (max-width: 599px) {
      @content;
    }
  } @else if $size == tablet-portrait-up {
    @media (min-width: 769px) {
      @content;
    }
  } @else if $size == tablet-portrait-down {
    @media (max-width: 768px) {
      @content;
    }
  } @else if $size == tablet-landscape-up {
    @media (min-width: 900px) {
      @content;
    }
  } @else if $size == tablet-landscape-down {
    @media (max-width: 900px) {
      @content;
    }
  } @else if $size == desktop-up {
    @media (min-width: 1200px) {
      @content;
    }
  } @else if $size == desktop-down {
    @media (max-width: 1199px) {
      @content;
    }
  } @else if $size == big-desktop-up {
    @media (min-width: 1800px) {
      @content;
    }
  }
}



// списиок

@mixin listReset() {
  list-style: none;
  margin: 0;
  padding: 0;
}
// ссылки

@mixin linkReset() {
  text-decoration: none;
}

// цвета

@function darker($color, $tone) {
  $adjust: -16 * $tone;
  @return adjust-color($color, $red: $adjust, $green: $adjust, $blue: $adjust);
}

@function lighter($color, $tone) {
  $adjust: 16 * $tone;
  @return adjust-color($color, $red: $adjust, $green: $adjust, $blue: $adjust);
}

//hex to rgba

// .element {<br>
// @include background-opacity(#000, 0.3);<br>
// }

@mixin background-opacity($color, $opacity: 0.5) {
  background: $color; /* На всякий случай */
  background: rgba($color, $opacity);
}

// адаптивные шрифты

@function strip-unit($number) {
  @if type-of($number) == "number" and not unitless($number) {
    @return $number / ($number * 0 + 1);
  }

  @return $number;
}

@function calcFluidFontSize($f-min, $f-max, $w-min, $w-max, $units: px) {
  $f-min: strip-unit($f-min);
  $f-max: strip-unit($f-max);
  $w-min: strip-unit($w-min);
  $w-max: strip-unit($w-max);

  $k: ($f-max - $f-min)/ ($w-max - $w-min);
  $b: $f-min - $k * $w-min;

  $b: $b + $units;

  @return calc(#{$k} * 100vw + #{$b});
}

@mixin fluidFontSize($f-min, $f-max, $w-min, $w-max, $fallback: false) {
  font-size: $f-min;

  @media (min-width: $w-min) {
    @if ($fallback) {
      font-size: $fallback;
    }

    font-size: calcFluidFontSize($f-min, $f-max, $w-min, $w-max, px);
  }

  @media (min-width: $w-max) {
    font-size: $f-max;
  }
}

// шрифты

@mixin ff($property, $map) {
  $length: length(map-keys($map));

  @if $length < 2 {
    @error "ff() $map requires at least values";
  }

  $map: map-sort($map);
  $keys: map-keys($map);
  #{$property}: map-get($map, nth($keys, 1));

  @for $i from 1 through $length - 1 {
    @media (min-width: nth($keys, $i)) {
      #{$property}: linear-interpolation(
        (
          nth($keys, $i): map-get($map, nth($keys, $i)),
          nth($keys, $i + 1): map-get($map, nth($keys, $i + 1))
        )
      );
    }
  }

  @media (min-width: nth($keys, $length)) {
    #{$property}: map-get($map, nth($keys, $length));
  }
}

@function linear-interpolation($map) {
  $keys: map-keys($map);

  @if length($keys) !=2 {
    @error "linear-interpolation() $map must be exactly 2 values";
  }

  $m: (map-get($map, nth($keys, 2)) - map-get($map, nth($keys, 1))) /
    (nth($keys, 2) - nth($keys, 1));
  $b: map-get($map, nth($keys, 1)) - $m * nth($keys, 1);
  $sign: "+";

  @if $b < 0 {
    $sign: "-";
    $b: abs($b);
  }

  @return calc(#{$m * 100}vw #{$sign} #{$b});
}

@function list-sort($list) {
  $sortedlist: ();

  @while length($list) >0 {
    $value: nth($list, 1);

    @each $item in $list {
      @if $item < $value {
        $value: $item;
      }
    }

    $sortedlist: append($sortedlist, $value, "space");
    $list: list-remove($list, index($list, $value));
  }

  @return $sortedlist;
}

@function map-sort($map) {
  $keys: list-sort(map-keys($map));
  $sortedMap: ();

  @each $key in $keys {
    $sortedMap: map-merge(
      $sortedMap,
      (
        $key: map-get($map, $key)
      )
    );
  }

  @return $sortedMap;
}

@function list-remove($list, $index) {
  $newList: ();

  @for $i from 1 through length($list) {
    @if $i !=$index {
      $newList: append($newList, nth($list, $i), "space");
    }
  }

  @return $newList;
}

// rem

/**
 * Rem-converter mixin to convert px to rem
 */
@mixin px-to-rem($pxValue, $property: "font-size", $standard-size: 16) {
  #{$property}: $pxValue + px;
  #{$property}: $pxValue/$standard-size + rem;
}

/**
 * Usages
 */

// // 1.1 Using it with font-size (default property)
// h1 {
//   @include px-to-rem(32);
// }

// // 1.2 Using it with padding (custom property)
// h1{
//   @include px-to-rem(20, padding);

// }

$rem-baseline: 16px !default;
$rem-fallback: false !default;
$rem-px-only: false !default;

@function rem-separator($list, $separator: false) {
  @if $separator == "comma" or $separator == "space" {
    @return append($list, null, $separator);
  }

  @if function-exists("list-separator") == true {
    @return list-separator($list);
  }

  // list-separator polyfill by Hugo Giraudel (https://sass-compatibility.github.io/#list_separator_function)
  $test-list: ();
  @each $item in $list {
    $test-list: append($test-list, $item, space);
  }

  @return if($test-list == $list, space, comma);
}

@mixin rem-baseline($zoom: 100%) {
  font-size: $zoom / 16px * $rem-baseline;
}

@function rem-convert($to, $values...) {
  $result: ();
  $separator: rem-separator($values);

  @each $value in $values {
    @if type-of($value) == "number" and unit($value) == "rem" and $to == "px" {
      $result: append($result, $value / 1rem * $rem-baseline, $separator);
    } @else if
      type-of($value) ==
      "number" and
      unit($value) ==
      "px" and
      $to ==
      "rem"
    {
      $result: append($result, $value / $rem-baseline * 1rem, $separator);
    } @else if type-of($value) == "list" {
      $value-separator: rem-separator($value);
      $value: rem-convert($to, $value...);
      $value: rem-separator($value, $value-separator);
      $result: append($result, $value, $separator);
    } @else {
      $result: append($result, $value, $separator);
    }
  }

  @return if(length($result) == 1, nth($result, 1), $result);
}

@function rem($values...) {
  @if $rem-px-only {
    @return rem-convert(px, $values...);
  } @else {
    @return rem-convert(rem, $values...);
  }
}

@mixin rem($properties, $values...) {
  @if type-of($properties) == "map" {
    @each $property in map-keys($properties) {
      @include rem($property, map-get($properties, $property));
    }
  } @else {
    @each $property in $properties {
      @if $rem-fallback or $rem-px-only {
        #{$property}: rem-convert(px, $values...);
      }
      @if not $rem-px-only {
        #{$property}: rem-convert(rem, $values...);
      }
    }
  }
}

// Function px to vw


// grid

$grid: (
  breakpoints: (
    xs: 0px,
    xsm: 500px,
    sm: 769px,
    md: 900px,
    lg: 1200px,
    xl: 1800px
  ),
  columns: 12,
  gap: 15px,
  container: 15px,
  box-sizing: border-box,
  mode: flex
);

@function fastGridListToMap($list, $settings) {
  $result: ();
  $i: 0;
  $breakpoints: map-get($settings, breakpoints);
  @each $breakpoint, $value in $breakpoints {
    $i: $i + 1;
    $value: if(length($list) >= $i, nth($list, $i), null);
    @if $value != null {
      $result: map-merge($result, ($breakpoint: $value));
    }
  }
  @return $result;
}

@function fastGridNormalizeSpans($spans, $settings) {
  $columns: map-get($settings, columns);
  @if type-of($spans) != map {
    $len: length($spans);
    @if $len >= 3 and nth($spans, $len - 1) == 'of' {
      $columns: nth($spans, $len);
      @if ($len > 3) {
        $spans: set-nth($spans, $len, null);
        $spans: set-nth($spans, $len - 1, null);
        $spans: fastGridListToMap($spans, $settings);
      } @else {
        $spans: nth($spans, 1);
      }
    } @else {
      $spans: fastGridListToMap($spans, $settings);
    }
  }
  @return $spans, $columns;
}

// get width for breakpoint
@function grid-width($breakpoint, $settings: null) {
  $settings: $grid !default;
  @return map-get(map-get($settings, breakpoints), $breakpoint);
}

// apply breakpoint rules
@mixin grid-breakpoint($breakpoint, $settings: null) {
  @media (min-width: grid-width($breakpoint, $settings)) {
    @content
  }
}

@mixin grid-container($from: null, $to: null, $settings: null) {
  $settings: if($settings != null, map-merge($grid, $settings), $grid);
  $gap: map-get($settings, container);
  $map: map_get($settings, breakpoints);
  width: 100%;
  margin-left: auto;
  margin-right: auto;

  @if type-of($gap) == number {
    padding-left: $gap;
    padding-right: $gap;
  }
  @if (type-of($gap) == list) {
    $gap: fastGridListToMap($gap, $settings);
  }

  $found: false;
  $break: false;
  @each $breakpoint, $minWidth in $map {
    @if $breakpoint == $from {
      $found: true;
    }
    @if $found == true and $break == false or (type-of($from) == list and index($from, $breakpoint) != null) {
      @if $minWidth > 0 {
        @media (min-width: $minWidth) {
          max-width: $minWidth;
        }
      } @else {
        max-width: $minWidth;
      }
    }
    @if type-of($gap) == map {
      $gapLocal: map-get($gap, $breakpoint);
      @if $minWidth > 0 {
        @media (min-width: $minWidth) {
          padding-left: $gapLocal;
          padding-right: $gapLocal;
        }
      } @else {
        padding-left: $gapLocal;
        padding-right: $gapLocal;
      }
    }
    @if $breakpoint == $to {
      $break: true;
    }
  }
}


@mixin grid-row($settings: null) {
  $settings: if($settings != null, map-merge($grid, $settings), $grid);
  $gap: map-get($settings, gap);
  $mode: map-get($settings, mode);

  @if ($mode == float) {
    &:before, &:after {
      content: ' ';
      display: table;
      clear: both;
    }
  } @else {
    display: flex;
    flex-flow: row wrap;
  }

  @if type-of($gap) == number {
    margin-left: -$gap;
    margin-right: -$gap;
  } @else {
    @if (type-of($gap) == list) {
      $gap: fastGridListToMap($gap, $settings);
    }
    @each $breakpoint, $value in $gap {
      $minWidth: grid-width($breakpoint, $settings);
      @if $minWidth > 0 {
        @media (min-width: $minWidth) {
          margin-left: -$value;
          margin-right: -$value;
        }
      } @else {
        margin-left: -$value;
        margin-right: -$value;
      }
    }
  }
}

@mixin grid-col-internal($box-sizing, $span, $columns, $gap, $gapLocal) {
  @if $span != null {
    $gapAlways: if($gapLocal == null, $gap, $gapLocal);
    width: 100% * $span / $columns - if($box-sizing == content-box and unit($gapAlways) == '%', $gapAlways * 2, 0);
    @if $box-sizing == content-box and unit($gapAlways) != '%' {
      box-sizing: border-box;
    }
  }
  padding-left: $gapLocal;
  padding-right: $gapLocal;
}

@mixin grid-col($spans, $settings: null) {
  $settings: if($settings != null, map-merge($grid, $settings), $grid);
  $gap: map-get($settings, gap);
  $box-sizing: map-get($settings, box-sizing);
  $mode: map-get($settings, mode);
  $map: map-get($settings, breakpoints);
  $spans: fastGridNormalizeSpans($spans, $settings);
  $columns: nth($spans, 2);
  $spans: nth($spans, 1);

  @if ($mode == float) {
    float: left;
  }

  @if type-of($gap) == number {
    padding-left: $gap;
    padding-right: $gap;
  } @else {
    @if (type-of($gap) == list) {
      $gap: fastGridListToMap($gap, $settings);
    }
  }

  @each $breakpoint, $minWidth in $map {
    $span: map-get($spans, $breakpoint);
    @if type-of($span) == list and nth($span, 2) == 'of' {
      $columnsLocal: nth($span, 3);
      $span: nth($span, 1);
    }
    $columnsLocal: $columns !default;

    $gapLocal: null;
    @if type-of($gap) == map {
      $gapLocal: map-get($gap, $breakpoint);
    }

    @if $minWidth > 0 {
      @media (min-width: $minWidth) {
        @include grid-col-internal($box-sizing, $span, $columnsLocal, $gap, $gapLocal);
      }
    } @else {
      @include grid-col-internal($box-sizing, $span, $columnsLocal, $gap, $gapLocal);
    }
  }
}


@mixin grid-prop($prop, $spans, $settings: null) {
  $settings: if($settings != null, map-merge($grid, $settings), $grid);
  $map: map-get($settings, breakpoints);
  $spans: fastGridNormalizeSpans($spans, $settings);
  $columns: nth($spans, 2);
  $spans: nth($spans, 1);

  @each $breakpoint, $minWidth in $map {
    $span: map-get($spans, $breakpoint);
    @if type-of($span) == list and nth($span, 2) == 'of' {
      $columnsLocal: nth($span, 3);
      $span: nth($span, 1);
    }
    $columnsLocal: $columns !default;

    @if $span != null {
      @if $minWidth > 0 {
        @media (min-width: $minWidth) {
          #{$prop}: 100% * $span / $columnsLocal;
        }
      } @else {
        #{$prop}: 100% * $span / $columnsLocal;
      }
    }
  }
}

@mixin grid-offset($spans, $settings: null) {
  @include grid-prop(margin-left, $spans, $settings);
}

@mixin grid-move($spans, $settings: null) {
  position: relative;
  @include grid-prop(left, $spans, $settings);
}
