.benefits {
  position: relative;

  text-align: center;
  &::before,
  &::after {
    content: "";
    position: absolute;
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 100%;
    width: 100px;
    height: 100px;
    z-index: 2;
  }
  &::before {
    bottom: 20%;
    left: 15px;
    background-image: url("../img/benefits/bg-box-left.png");
  }
  &::after {
    top: 40%;
    right: 0;
    height: 200px;
    width: 200px;
    background-image: url("../img/benefits/bg-box-right.png");
  }
  @include for-size(tablet-portrait-up) {
    padding: 5rem 0;
  }

  & .container {
    padding-top: 2rem;
    background-image: url("../img/benefits/bg-line.png");
    background-repeat: no-repeat;
    background-position: 5% 0;
    background-size: 50%;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    margin: 0;
    list-style-type: none;

    @include grid-row();

    @include for-size(tablet-portrait-up) {
      margin: 6rem 0 0;
    }
    @include for-size(desktop-up) {
      margin: 10rem 0 0;
    }
  }
  &__item {
    position: relative;
    display: block;
    padding-top: 5rem;
    @include ff("margin-bottom", $mb-size);

    @include grid-col(
      (
        xs: 12,
        xsm: 6,
        sm: 6,
        md: 4
      )
    );

    &:last-child {
      margin-bottom: 0;
    }

    @include for-size(tablet-portrait-up) {
      &:last-child {
        @include ff("margin-bottom", $mb-size);
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      -webkit-transform: translateX(-50%);
      -moz-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
      -o-transform: translateX(-50%);

      width: 100%;
      height: 4rem;

      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }

    &:nth-child(1)::before {
      background-image: url("../img/icons/icon-01.png");
    }
    &:nth-child(2)::before {
      background-image: url("../img/icons/icon-03.png");
    }
    &:nth-child(3)::before {
      background-image: url("../img/icons/icon-05.png");
    }
    &:nth-child(4)::before {
      background-image: url("../img/icons/icon-02.png");
    }
    &:nth-child(5)::before {
      background-image: url("../img/icons/icon-04.png");
    }
    &:nth-child(6)::before {
      background-image: url("../img/icons/icon-06.png");
    }

    & span {
      display: block;

      max-width: 18rem;
      margin: 0 auto;

      @include for-size(tablet-portrait-up) {
        font-size: 1.5rem;
      }
    }
  }
}
